/**
 * 
 * @param {PIXI app} app 
 * @returns PIXI.Container
 */
exports.createDungeonScene = function(app){
    const dungeonScene = new PIXI.Container();
    app.stage.addChild(dungeonScene);
    const titleScene = new PIXI.Container();
    titleScene.visible=false;
    const basicText = new PIXI.Text('');
    basicText.x = 50;
    basicText.y = 50;
    app.stage.addChild(basicText);

    return dungeonScene;
}
